import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { RootState } from "../app/store"

export type UserConnected = boolean;

export interface userState {
  userConnected: UserConnected,
  account: string,
  buyNumber: number,
  task1ClaimStatus: boolean,
  childrenNumber: number,
  task2ClaimStatus: false,
  guildReward: number,
  guildChildrenList: string[],
  guildBuyNumber: number,
  guildRewardClaimStatus: [boolean, boolean, boolean, boolean],
  rate: number,
  hasGuild: boolean,
  totalNumber: number,
}

const initialState: userState = {
  userConnected: false,
  account: '',
  buyNumber: 0,
  task1ClaimStatus: false,
  childrenNumber: 0,
  task2ClaimStatus: false,
  guildReward: 0,
  guildChildrenList: [],
  guildBuyNumber: 0,
  guildRewardClaimStatus: [false, false, false, false],
  rate: 0,
  hasGuild: false,
  totalNumber: 0,
}

export const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    connect: (state) => {
      state.userConnected = true;
    },
    account: (state, action) => {
      state.account = action.payload;
    },
    info: (state, action) => {
      state.rate = action.payload[2] / 100;
      state.hasGuild = action.payload[4];
    },
    buyNumber: (state, action) => {
      state.buyNumber = action.payload;
    },
    totalNumber: (state, action) => {
      state.totalNumber = action.payload;
    },
    childrenNumber: (state, action) => {
      state.childrenNumber = action.payload;
    },
    task1: (state, action) => {
      state.task1ClaimStatus = action.payload;
    },
    task2: (state, action) => {
      state.task2ClaimStatus = action.payload;
    }

  },
})

export const {
  connect,
  account,
  info,
  buyNumber,
  childrenNumber,
  task1,
  task2,
  totalNumber,
} = userSlice.actions;

export const userState = (state: RootState) => state.user

export default userSlice.reducer