import { Tabs } from '@mui/base/Tabs';
import { TabsList } from '@mui/base/TabsList';
import { TabPanel } from '@mui/base/TabPanel';
import { Tab } from '@mui/base/Tab';

import { useEffect, useState } from 'react';
import GuildPage from './guildpage';
import { getChildrenList, getPersonalInfo } from '../features/api';

import PersonInfo from './personInfo'
import TaskList from './taskList'
import { updateBasicInfo } from '../features/common';
import { useAppSelector } from '../app/hooks';
import { userState } from '../features/userSlice';

export function InfoPage() {
  const [selectedTab, setSelectedTab] = useState(1);

  const onTabChanged = (event: any, newValue: any) => {
    setSelectedTab(newValue);
  }

  const user = useAppSelector(userState);
  const [childrenList, setChildrenList] = useState([]);

  useEffect(() => {
    updateBasicInfo();
    getChildrenList().then(list => {
      setChildrenList(list);
    });
  
  }, [user.account]);

  const mTabClassSelected = {
    root: { 
      className: 'text-black text-sm font-normal text-center h-7 bg-gradient-to-r from-teal-300 to-yellow-300 rounded-3xl'
    }
  };

  const mTabClass = {
    root: { 
      className: 'text-black text-sm font-normal text-center h-7 bg-black bg-opacity-40 rounded-3xl'
    }
  };

  const mTabList = {
    root: {
      className: 'flex justify-between pl-9 pr-9',
    }
  }

  return <div className="w-full bg-opacity-50 pt-2 pl-5 pr-5 pb-5">
    <Tabs defaultValue={1} onChange={onTabChanged} value={selectedTab}>
      <TabsList slotProps={mTabList}>
        <Tab value={1} slotProps={selectedTab === 1 ? mTabClassSelected : mTabClass}><span className='pl-3 pr-3'>Guild</span></Tab>
        <Tab value={2} slotProps={selectedTab === 2 ? mTabClassSelected : mTabClass}><span className='pl-3 pr-3'>Personal Info</span></Tab>
        <Tab value={3} slotProps={selectedTab === 3 ? mTabClassSelected : mTabClass}><span className='pl-3 pr-3'>Task List</span></Tab>
      </TabsList>
      <div className="h-px bg-neutral-200 bg-opacity-20 w-full mt-2 mb-3" />
      <TabPanel value={1}><GuildPage members={childrenList}></GuildPage></TabPanel>
      <TabPanel value={2}><PersonInfo members={childrenList}></PersonInfo></TabPanel>
      <TabPanel value={3}><TaskList></TaskList></TabPanel>
    </Tabs>
  </div>
}

export default InfoPage;