import { BrowserProvider, ethers, parseEther } from 'ethers';
import relationAbi from '../abi/relation.json';
import salesAbi from '../abi/sale.json';
import NFTAbi from '../abi/nft.json';
import { account, connect } from './userSlice';
import { store } from '../app/store';
import { myAlert, paramsFromURL } from '../util';
import { updateBasicInfo } from './common';

// const contractRelationAddress = '0x4203E977BF9378808A3FFaA356E4b0e1ddb49F4b';
// const contractSalesAddress = '0xf18d774908754Fb4110b14b0c1446854d0db59b0';
// const contractNFTAddress = '0xd234756212D1Eb01500769fC5F9bd801B41BfEA5';

// For test - 
const contractRelationAddress = '0x6Cd23D2e1FfD79D5f80b1D3dA4d3a6759e118385';
const contractSalesAddress = '0x822CcB60350ddae2cD74AaB118ec8bf9B78e7CF0';
const contractNFTAddress = '0xeF1F24230C79F4e2Fa9d47328e1017058fEC8373';
let provider: BrowserProvider = new ethers.BrowserProvider(window.ethereum);;

declare global {
  interface Window {
    ethereum?: any
  }
}

(async () => {
  const { ethereum } = window;
  if (!ethereum) {
    alert("Get metamask!");
    return;
  }
  ethereum.on('accountsChanged', (accounts: string[]) => {
    console.log('on accounts changed:', accounts);
    store.dispatch(account(accounts[0]));
    updateBasicInfo();
  });

  const accounts = await provider.send("eth_requestAccounts", []);
  store.dispatch(account(accounts[0]));
})()



export async function connectWallet() {
  if (typeof window.ethereum !== "undefined") {
    
    const accounts = await provider.send("eth_requestAccounts", []);
    const account = accounts[0];
    store.dispatch(connect());
    return account;
  } else {
    myAlert("Please install MetaMask", 1);
  }
}

export async function mintBlindBox() {
  try {
    
    const signer = await provider.getSigner();
    const salesContract = new ethers.Contract(contractSalesAddress, salesAbi, signer);

    let parent = paramsFromURL.address;
    const [myAddr, parentAddr]: [string, string] = await getPersonalInfo();
    if (parentAddr != "0x0000000000000000000000000000000000000000") {
      parent = parentAddr;
    }
    if (!parent) throw Error("no parent");
    console.log("par: ", parent);
    if (paramsFromURL.guild) {
      const result = await salesContract.mintS(parent, {value: parseEther("0.065")});
      console.log('mints Function result:', result);
    } else {
      const result = await salesContract.mint(parent, {value: parseEther("0.065")});
      console.log('mint Function result:', result);
    }

    return true;
  } catch (error: any) {
    if (error && error.reason) {
      myAlert(error.reason, 1);
    } else myAlert("Error" + error, 1);
    console.error('Error:', error);
    return false;
  }
}

export async function joinGuild() {
  try {
    
    const signer = await provider.getSigner();
    const relationsContract = new ethers.Contract(contractRelationAddress, relationAbi, signer);
    console.log("join guild par:" , paramsFromURL.address);
    const result = await relationsContract.join(paramsFromURL.address);
    console.log('joinGuild Function result:', result);
    return;
  } catch (error) {
    console.error('Error:', error);
    
    myAlert("Error" + error, 1);
  }
}

export async function getPersonalInfo() {
  try {
    
    const signer = await provider.getSigner();
    const relationContract = new ethers.Contract(contractRelationAddress, relationAbi, signer);
    let result = await relationContract.findInfo(signer.getAddress());
    result = result.map((v: any) => {
      if (typeof v === "bigint") {
        return Number(v);
      }
      return v;
    })
    console.log('findInfo Function result:', result);
    return result;
  } catch (error) {
    console.error('Error:', error);
    myAlert("Error" + error);
  }
}

