export async function setRate(addr: string, rate: number) {
  try {
    
    const signer = await provider.getSigner();
    const relationContract = new ethers.Contract(contractRelationAddress, relationAbi, signer);
    console.log('setRate called:', addr, BigInt(rate * 100));
    let result = await relationContract.setRate(addr, BigInt(rate * 100));
    console.log('setRate Function result:', result);

    return result;
  } catch (error: any) {
    console.error('Error:', error);
    myAlert(error?.reason, 1);
  }
}