import { ethers } from "ethers";
import copy from 'copy-to-clipboard';

const STOP_ALERT = true;

const TARGET_TIME = new Date('2023-10-29T00:00:00').getTime(); 

export const paramsFromURL = {
  guild: false,
  address: '',
}

export function sliceStr(str: string, front: number, back: number) {
  return str.slice(0, front) + "..." + str.slice(-1 * back);
}

export function getRandomPubKeys(n: number) {
  let keyList = [];

  for (let i = 0; i < n; i++) {
    let str = '0x' + getRandomKey(32);
    keyList.push(str);
  }

  return keyList;
}

function getRandomKey(length: number) {

  const randomPrivateKey = ethers.Wallet.createRandom().privateKey;
  const wallet = new ethers.Wallet(randomPrivateKey);
  const address = wallet.address;

  return address;
}

export function shuffleArray(array: any[]) {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  return array;
}

function isValidEthereumAddress(address: string) {
  if (/^(0x)?[0-9a-fA-F]{40}$/.test(address)) {
    return true;
  } else {
    return false;
  }
}

export function isGuildLink() {
  const queryString = window.location.search;

  const queryStringWithoutQuestionMark = queryString.slice(1);

  const urlParams = new URLSearchParams(queryStringWithoutQuestionMark);

  let isGuild = urlParams.get('guild');
 
  if (!isGuild) {
    isGuild = urlParams.get('amp;guild');
  }

  if (isGuild === 'true') {
    paramsFromURL.guild = true;
    return true;
  }
  else {
    paramsFromURL.guild = false;
    return false;
  }
}

export function getParentAddress() {
  const queryString = window.location.search;

  const queryStringWithoutQuestionMark = queryString.slice(1);

  const urlParams = new URLSearchParams(queryStringWithoutQuestionMark);

  let address = urlParams.get('address');

  if (!address) {
    address = urlParams.get('amp;address');
  }

  paramsFromURL.address = address || '';
  return address;
}

export function genShareLink(guild: boolean, address: string) {
  let url = location.protocol + '//' + location.host + location.pathname + '?';
  if (guild) {
    url += "guild=true&"
  }
  url += "address=" + address;

  copy(url);

  alert("Share link is copied!!");
  return url;
}

export function widthStyle(n: number) {
  return {
    width: Math.floor(n) + '%',
  }
}

export function calcWidthStyle(n1: number, n2: number) {
  return widthStyle((n1 <= n2) ? (100 * n1 / n2) : 100);
}

export function genPercent(n1: number, n2: number) {
  return (n1 < n2 ? n1 : n2) + '/' + n2;
}

export function myAlert(str: any, level=0) {
  if (level === 1) alert(str);
  else if (STOP_ALERT) return;
  else alert(str);
}

export function updateCountdown() {
  const currentDate = new Date().getTime(); // 获取当前时间的时间戳
  const timeRemaining = TARGET_TIME - currentDate;

  if (timeRemaining <= 0) {
    return ['00','00','00','00'];
  } else {
    let days = Math.floor(timeRemaining / (1000 * 60 * 60 * 24));
    let hours = Math.floor((timeRemaining % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    let minutes = Math.floor((timeRemaining % (1000 * 60 * 60)) / (1000 * 60));
    let seconds = Math.floor((timeRemaining % (1000 * 60)) / 1000);

    let ret = [days, hours, minutes, seconds];
    return ret.map(v => {
      if (v < 10) return '0' + v;
      else return '' + v;
    });
  }
}