
import { Modal as BaseModal } from '@mui/base/Modal';
import React, { useImperativeHandle } from 'react'
import clsx from 'clsx';
import { styled, Box, Theme } from '@mui/system';
import LikeImg from '../images/Unionlike.png'
import { useState } from 'react';
import CloseImg from '../images/close.png'
import ShageImg from '../images/ShareImg.svg'
import { genShareLink } from '../util';
import { useAppDispatch, useAppSelector } from '../app/hooks';
import { userState } from '../features/userSlice';
import { choose } from '../features/curpageSlice';
import BG2 from "../images/nav2.png"
import Icon2 from "../images/navIcon2.svg"
import FrameImg from "../images/frame.png";

let timer: any;
const NavbarGuildModal = 
  (
    {jumpPage}: {jumpPage: (n:number) => any}
  ) => {
    const [open, setOpen] = useState(false);
    const user = useAppSelector(userState);

    const handleOpen = () => {
      if (user.hasGuild)
        jumpPage(1)
      else {
        setOpen(true);
      }
      timer = setTimeout(() => {
          handleClose();
      }, 5000);
    }
    const handleClose = () => {
      clearTimeout(timer);
      setOpen(false);
    }
    const dispatch = useAppDispatch();

    
    return <div className='relative w-64 h-14 mt-4'>
      <TriggerButton onClick={handleOpen}>
        <img src={BG2} className="w-64 h-14 absolute top-0 -z-1 object-cover" alt="" />
        <div className="w-64 h-14 absolute top-0 bg-black to-black bg-opacity-50 flex items-center">
          <img src={Icon2} className="w-8 h-8 ml-6" alt="" />
          <div className="ml-3 text-white text-sm font-normal font-['PingFang SC']">Guild</div>
        </div>
        <img src={FrameImg} className="w-64 h-14 absolute top-0" alt="" />
      </TriggerButton>
      <Modal
            aria-labelledby="unstyled-modal-title"
            aria-describedby="unstyled-modal-description"
            open={open}
            onClose={handleClose}
            slots={{ backdrop: StyledBackdrop }}
        >
            <Box sx={style}>
                <div className='w-72 bg-[#1A1F2C] relative py-3 px-2'>
                    <p id="unstyled-modal-title" className='text-white text-center text-base'>You have not joied any guilds yet.</p>
                    <p id="unstyled-modal-title" className='text-white text-center text-base'>If you would like to apply for a guild, please join our official telegram group and contact the administrator! Thank you for your support!</p>
                    

                    <div onClick={() => {handleClose(); jumpPage(0)}} className='text-[#1D1C16] mt-5 bg-[#3BFDEC] w-full h-8 rounded-lg flex items-center justify-center'>OK</div>
                </div>

            </Box>
        </Modal></div>
}
// )
export default NavbarGuildModal;

const Backdrop = React.forwardRef<
    HTMLDivElement,
    { open?: boolean; className: string }
>((props, ref) => {
    const { open, className, ...other } = props;
    return (
        <div
            className={clsx({ 'MuiBackdrop-open': open }, className)}
            ref={ref}
            {...other}
        />
    );
});

const blue = {
    200: '#99CCF3',
    400: '#3399FF',
    500: '#007FFF',
};

const grey = {
    50: '#f6f8fa',
    100: '#eaeef2',
    200: '#d0d7de',
    300: '#afb8c1',
    400: '#8c959f',
    500: '#6e7781',
    600: '#57606a',
    700: '#424a53',
    800: '#32383f',
    900: '#24292f',
};

const Modal = styled(BaseModal)`
  position: fixed;
  z-index: 1300;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const StyledBackdrop = styled(Backdrop)`
  z-index: -1;
  position: fixed;
  inset: 0;
  background-color: rgb(0 0 0 / 0.5);
  -webkit-tap-highlight-color: transparent;
`;

const style = (theme: Theme) => ({
    borderRadius: '12px',
    padding: '16px 32px 24px 32px',
    backgroundColor: '#1A1F2C',


});

