import UnionLogo from '../images/Union.svg';
import OpenNav from '../images/openNav.svg';
import NavBarIcon from '../images/navbaricon.svg';
import GlobalIcon from "../images/yuyan-1.svg";
import ARBIcon from "../images/ellipse-22.svg";
import ReturnIcon from "../images/fanhui 1.svg";
import FrameImg from "../images/frame.png";
import Icon1 from "../images/navIcon1.svg"
import Icon2 from "../images/navIcon2.svg"
import Icon3 from "../images/navIcon3.svg"
import Icon4 from "../images/navIcon4.svg"
import BG1 from "../images/nav1.png"
import BG2 from "../images/nav2.png"
import BG3 from "../images/nav3.png"
import BG4 from "../images/nav4.png"

import { CurpageType, choose, curpageState, selectCurpage } from '../features/curpageSlice';
import { useAppSelector, useAppDispatch } from '../app/hooks';
import { userState } from '../features/userSlice';
import { Modal } from '@mui/base/Modal';
import React, { useRef } from 'react';
import clsx from 'clsx';
import NavbarGuildModal from './NavbarGuildModal';

export function NavBar() {
  const dispatch = useAppDispatch();
  const curpage = useAppSelector(selectCurpage);
  const account = useAppSelector((state) => {
    if (!state.user.account) return "DISCONNECTED";
    let str = state.user.account;
    return str.slice(0, 6) + '...' + str.slice(-4);
  });

  const [open, setOpen] = React.useState(false);
  const [open2, setOpen2] = React.useState(false);

  const handleClose = () => setOpen(false);

  const user = useAppSelector(userState);

  const handleOpenNavigator = () => {
    setOpen(!open);
  }

  const jumpPage = (n: CurpageType) => {
    if (n === curpage) {
    } else {
      dispatch(choose(n));
      console.log("dispatch", n);
    }
    handleClose();
  }

  return <div className="w-full h-12 bg-white bg-opacity-50 flex justify-between">
    <div id="navbar-left" className="flex items-center">
      <img className="w-4 h-3.5 mr-4 ml-3" src={open ? OpenNav : UnionLogo} onClick={handleOpenNavigator}/>
      <div className="w-7 h-7 bg-slate-950 rounded flex items-center justify-center">
        <img className="w-3.5 h-4" src={NavBarIcon}></img>
      </div>
    </div>
    <div id="navbar-right" className="flex items-center">
      <div id="language" className='flex items-center'>
        <img src={GlobalIcon} className='mr-1 w-4 h-4' />
        <div className="text-gray-950 text-base font-medium font-['Inter'] mt-1 mr-4">En</div>
      </div>
      <img id="navbar-arb-icon" className="w-7 h-7 rounded-full mr-4" src={ARBIcon}></img>
      <div className="w-28 h-7 mr-3 bg-neutral-600 rounded-2xl flex items-center justify-center">
        <div className="text-white text-xs font-medium font-['PingFang SC']">{account}</div>
      </div>
      <img className='w-2.5 h-2.5 mr-4' src={ReturnIcon}></img>
    </div>
    <Modal
        aria-labelledby="unstyled-modal-title"
        aria-describedby="unstyled-modal-description"
        open={open}
        onClose={handleClose}
        slots={{ backdrop: Backdrop }}
      >
        <div className="fixed z-50 flex justify-center items-center flex-col top-12 border-0 outline-none">
          <div onClick={() => {jumpPage(0)}} className="relative w-64 h-14 mt-4">
            <img src={BG1} className="w-64 h-14 absolute top-0 -z-1 object-cover" alt="" />
            <div className="w-64 h-14 absolute top-0 bg-black to-black bg-opacity-50 flex items-center">
              <img src={Icon1} className="w-8 h-8 ml-6" alt="" />
              <div className="ml-3 text-white text-sm font-normal font-['PingFang SC']">Mint</div>
            </div>
            <img src={FrameImg} className="w-64 h-14 absolute top-0" alt="" />
          </div>
          {/* <div onClick={() => {user.hasGuild ? jumpPage(1) : setOpen2(true)}} className="relative w-64 h-14 mt-4">
            <img src={BG2} className="w-64 h-14 absolute top-0 -z-1 object-cover" alt="" />
            <div className="w-64 h-14 absolute top-0 bg-black to-black bg-opacity-50 flex items-center">
              <img src={Icon2} className="w-8 h-8 ml-6" alt="" />
              <div className="ml-3 text-white text-sm font-normal font-['PingFang SC']">Guild</div>
            </div>
            <img src={FrameImg} className="w-64 h-14 absolute top-0" alt="" />
            <NavbarGuildModal open={open2} setOpen={function (n: boolean) {
            updateChildState(n);
          } } ></NavbarGuildModal>

          </div> */}
          <NavbarGuildModal jumpPage={jumpPage}></NavbarGuildModal>
          <div onClick={() => {jumpPage(2)}} className="relative w-64 h-14 mt-4">
            <img src={BG3} className="w-64 h-14 absolute top-0 -z-1 object-cover" alt="" />
            <div className="w-64 h-14 absolute top-0 bg-black to-black bg-opacity-50 flex items-center">
              <img src={Icon3} className="w-8 h-8 ml-6" alt="" />
              <div className="ml-3 text-white text-sm font-normal font-['PingFang SC']">My bag</div>
            </div>
            <img src={FrameImg} className="w-64 h-14 absolute top-0" alt="" />
          </div>
          <div onClick={() => {jumpPage(3)}} className="relative w-64 h-14 mt-4">
            <img src={BG4} className="w-64 h-14 absolute top-0 -z-1 object-cover" alt="" />
            <div className="w-64 h-14 absolute top-0 bg-black to-black bg-opacity-50 flex items-center">
              <img src={Icon4} className="w-8 h-8 ml-6" alt="" />
              <div className="ml-3 text-white text-sm font-normal font-['PingFang SC']">Announcement</div>
            </div>
            <img src={FrameImg} className="w-64 h-14 absolute top-0" alt="" />
          </div>
        </div>
    </Modal>
  </div>
}

const Backdrop = React.forwardRef<
  HTMLDivElement,
  { open?: boolean; className: string }
>((props, ref) => {
  // @ts-ignore
  const { open, className, ownerState, ...other } = props;
  return (
    <div
      className={
        clsx(
          // { 'MuiBackdrop-open': open }, 
          className,
          'z-40 fixed inset-0 bg-opacity-90 bg-black top-12')
      }
      ref={ref}
      {...other}
    />
  );
});


export default NavBar;