import BagImg1 from "../images/mybag1.svg";
import BagImg2 from "../images/homepage2.png";
import DogCard from "./dogcard";
import { useAppSelector } from "../app/hooks";
import { userState } from "../features/userSlice";

function BagPage() {
  const user = useAppSelector(userState);
  
  return <div className="w-full flex flex-col content-center items-center">
    <div className="mt-3 text-white text-lg font-medium font-['PingFang SC']">My Bag</div>
    <div className="w-90 mt-3 bg-slate-950 bg-opacity-90 rounded-lg flex flex-col content-center items-center">
      <div className="w-85 h-56 mt-2.5 bg-gray-900 rounded-lg flex flex-col content-center items-center">
        <div className="w-72 h-10 mt-4 leading-10 bg-[#0D1019] rounded text-center text-white text-base font-medium font-['PingFang SC']">
          My NFT
        </div>
        <img className="mt-8 w-12.5 h-18.75" src={BagImg1} alt="" />
        <div className="mt-4 mb-10 text-neutral-500 text-xs font-medium font-['PingFang SC']">You don’t own NFT yet</div>
      </div>
      <div className="w-85 mt-2.5 bg-gray-900 rounded-lg flex flex-col content-center items-center">
        <div className="w-72 h-10 mt-4 leading-10 bg-[#0D1019] rounded text-center text-white text-base font-medium font-['PingFang SC']">
          My Blind Box
        </div>
        <div className="w-72 mt-4 mb-7 bg-[#0D1019] rounded flex items-center flex-col">
          <div className="mt-4 w-64 h-48 bg-gradient-to-b from-stone-800 to-gray-900 rounded-lg  flex items-center flex-col">
            <img className="mt-5 mb-8 w-32 h-32" src={BagImg2} alt="" />
          </div>
          <div className="flex px-4 mt-4 justify-between items-center w-full">
            <div className="text-white text-base font-medium font-['PingFang SC']">Blind Box</div>
            <div className="text-white text-base font-semibold font-['PingFang SC'] tracking-wide">{'x' + user.totalNumber}</div>
          </div>
          <div className="mb-4 mt-4 w-64 h-12 leading-12 bg-teal-300 rounded shadow-inner text-center text-stone-900 text-base font-medium font-['PingFang SC']">
            Open
          </div>
        </div>
      </div>
      <div className="w-85 mt-2.5 mb-2.5 bg-gray-900 rounded-lg flex flex-col content-center items-center">
        <div className="w-72 h-10 mt-4 leading-10 bg-[#0D1019] rounded text-center text-white text-base font-medium font-['PingFang SC']">
          Introduction
        </div>
        <div className="mt-4 mb-4 w-72 bg-[#0D1019] rounded-lg">
          <div className="flex justify-between px-3 w-full">
            <DogCard name="red"></DogCard>
            <DogCard name="green"></DogCard>
          </div>
          <div className="flex justify-between px-3 w-full">
            <DogCard name="blue"></DogCard>
            <DogCard name="white"></DogCard>
          </div>
          <div className="mb-4 flex justify-between px-3 w-full">
            <DogCard name="yellow"></DogCard>
            <DogCard name="purple"></DogCard>
          </div>
        </div>
      </div>
    </div>
  </div>
}

export default BagPage;