
import { Modal as BaseModal } from '@mui/base/Modal';
import React from 'react'
import clsx from 'clsx';
import { styled, Box, Theme } from '@mui/system';
import LikeImg from '../images/Unionlike.png'
import { useState } from 'react';
import { setRate } from '../features/api';


const PersonInfoModal = ({rate, member}: {rate: number, member: string}) => {
    const [open, setOpen] = useState(false);
    const [radioId,setId] = useState(10)
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const handleSave = () => {
        setRate(member, radioId);
        setOpen(false);
    }

    let getRateList = () => {
        const rateList = [];
        for (let i = 10; i < rate; i+=10) {
            rateList.push(i);
        }
        return rateList;
    }
    return <div>
        <TriggerButton onClick={handleOpen}>
        Setting
        </TriggerButton>
        <Modal
            aria-labelledby="unstyled-modal-title"
            aria-describedby="unstyled-modal-description"
            open={open}
            onClose={handleClose}
            slots={{ backdrop: StyledBackdrop }}
        >
            <Box sx={style}>
                <div className='w-72 bg-[#1A1F2C]'>
                    <h2 id="unstyled-modal-title" className='text-white'>Setting</h2>
                    {getRateList().map(r => <div key={r} className='mt-2 h-5 flex items-center text-white'>
                        {radioId === r ? <div className='h-3 w-3 rounded-full bg-[#3BFDEC] flex justify-center items-center'>
                            <img className="w-1" src={LikeImg} alt="" /> </div> : <input className='h-3 w-3' type="radio" name="10%" id="radio1" onClick={() => { setId(r) }} />}
                        <div className={radioId === r ?'ml-2 text-[#3BFDEC]':'ml-2' }>{r+'%'}</div>
                    </div>)}
                    <div className='flex mt-4 justify-between'>
                        <div onClick={handleClose} className='text-white bg-[#1A1F2C] w-32 h-8 rounded-lg border border-white flex items-center justify-center'>Cancel</div>
                        <div onClick={() => { handleSave }} className='text-[#1D1C16] rounded-lg w-32 h-8 flex justify-center items-center bg-[#3BFDEC]'>Save</div>
                    </div>
                </div>

            </Box>
        </Modal>

    </div>
}
export default PersonInfoModal
const Backdrop = React.forwardRef<
    HTMLDivElement,
    { open?: boolean; className: string }
>((props, ref) => {
    const { open, className, ...other } = props;
    return (
        <div
            className={clsx({ 'MuiBackdrop-open': open }, className)}
            ref={ref}
            {...other}
        />
    );
});

const blue = {
    200: '#99CCF3',
    400: '#3399FF',
    500: '#007FFF',
};

const grey = {
    50: '#f6f8fa',
    100: '#eaeef2',
    200: '#d0d7de',
    300: '#afb8c1',
    400: '#8c959f',
    500: '#6e7781',
    600: '#57606a',
    700: '#424a53',
    800: '#32383f',
    900: '#24292f',
};

const Modal = styled(BaseModal)`
  position: fixed;
  z-index: 1300;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const StyledBackdrop = styled(Backdrop)`
  z-index: -1;
  position: fixed;
  inset: 0;
  background-color: rgb(0 0 0 / 0.5);
  -webkit-tap-highlight-color: transparent;
`;

const style = (theme: Theme) => ({
    //   width: 400,
    borderRadius: '12px',
    padding: '16px 32px 24px 32px',
    backgroundColor: '#1A1F2C',


    // boxShadow: `0px 2px 24px ${theme.palette.mode === 'dark' ? '#000' : '#383838'}`,
});

const TriggerButton = styled('button')(
    ({ theme }) => `
  font-family: IBM Plex Sans, sans-serif;
  font-size: 0.75rem;
  line-height: 1rem;
  box-sizing: border-box;
  border-radius: 0.5rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  background: white;
  border: 1px solid ${theme.palette.mode === 'dark' ? grey[800] : grey[200]};
  color: ${theme.palette.mode === 'dark' ? grey[100] : grey[900]};

  &:hover {
    background: ${theme.palette.mode === 'dark' ? grey[800] : grey[50]};
    border-color: ${theme.palette.mode === 'dark' ? grey[600] : grey[300]};
  }

  &:focus-visible {
    border-color: ${blue[400]};
    outline: 3px solid ${theme.palette.mode === 'dark' ? blue[500] : blue[200]};
  }
  `,
);