import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { RootState } from "../app/store"

export enum CurpageType {"home", "guild", "mybag", "announcement"}

export interface curpageState {
  value: CurpageType,
}

const initialState: curpageState = {
  value: 0,
}

export const curpageSlice = createSlice({
  name: "curpage",
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    // Use the PayloadAction type to declare the contents of `action.payload`
    choose: (state, action: PayloadAction<CurpageType>) => {
      state.value = action.payload
    },
  },
})

export const { choose } = curpageSlice.actions

export const selectCurpage = (state: RootState) => state.curpage.value

export default curpageSlice.reducer