import GreenDog from '../images/green.png';
import RedDog from '../images/red.png';
import WhiteDog from '../images/white.png';
import PurpleDog from '../images/purple.png';
import BlueDog from '../images/blue.png';
import YellowDog from '../images/yellow.png';
import { useEffect, useState } from 'react';

const dogs = {
  green: {
    name: 'GREEN',
    level: 'SS',
    prob: '0.018%',
    img: GreenDog,
    left: 'left-4'
  },
  red: {
    name: 'RED',
    level: 'SSS',
    prob: '0.002%',
    img: RedDog,
    left: 'left-4',
  },
  blue: {
    name: 'BLUE',
    level: 'S',
    prob: '0.18%',
    img: BlueDog,
    left: 'left-8'
  },
  white: {
    name: 'WHITE',
    level: 'A',
    prob: '0.8%',
    img: WhiteDog,
    left: 'left-4'
  },
  yellow: {
    name: 'YELLOW',
    level: 'B',
    prob: '4%',
    img: YellowDog,
    left: 'left-4'
  },
  purple: {
    name: 'PURPLE',
    level: 'C',
    prob: '95%',
    img: PurpleDog,
    left: 'left-8'
  }
}


function DogCard ({name}: {name: string}) {
  const [curDog, setCurDog] = useState(dogs.purple);

  useEffect(() => {
    switch (name) {
      case 'green':
        setCurDog(dogs.green);
        break;
      case 'purple':
        setCurDog(dogs.purple);
        break;
      case 'yellow':
        setCurDog(dogs.yellow);
        break;
      case 'white':
        setCurDog(dogs.white);
        break;
      case 'blue':
        setCurDog(dogs.blue);
        break;
      case 'red':
        setCurDog(dogs.red);
        break;
    }
  }, [name])
  
  return <div  className={`w-32 h-52 rounded-md relative bg-[#0D1019] shrink-0`}>
      <img src={curDog.img} className={`absolute z-10 h-28 bottom-20 ${curDog.left}`} alt="" />
      <div className={`z-0 w-32 h-40 rounded-md absolute bottom-0 ${curDog.name.toLocaleLowerCase()}-dog flex flex-col items-center`}>
        <div className="mt-24 text-right text-white text-xs font-medium font-['PingFang SC']">{curDog.name}</div>
        <div className="w-full px-3 mt-1 flex justify-between">
          <div className="text-white text-opacity-70 text-xs font-normal font-['PingFang SC']">Level</div>
          <div className="text-white text-opacity-70 text-xs font-normal font-['PingFang SC']">{curDog.level}</div>
        </div>
        <div className="w-full px-3 mt-1.5 mb-3 flex justify-between">
          <div className="text-white text-opacity-70 text-xs font-normal font-['PingFang SC']">Rate</div>
          <div className="text-white text-opacity-70 text-xs font-normal font-['PingFang SC']">{curDog.prob}</div>
        </div>
      </div>
    </div>
}

export default DogCard;