import React from "react"
import ReactDOM from "react-dom/client"
import { Provider } from "react-redux"
import { store } from "./app/store"
import App from "./App"
import "./index.css"
import { isGuildLink, getParentAddress } from "./util"

isGuildLink();
getParentAddress();

const RootEl = document.getElementsByTagName('html')[0];
let fontSize = RootEl.clientWidth / 393 * 16;
fontSize = fontSize > 96 ? 96 : fontSize;
RootEl.style.fontSize = fontSize + 'px';

ReactDOM.createRoot(document.getElementById("root")!).render(
  <React.StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
  </React.StrictMode>,
)
