import { useEffect, useState } from 'react'
import HomePageImg1 from '../images/homepage1.svg'
import HomePageImg2 from '../images/homepage2.png'
import HomePageImg3 from '../images/homepage3.svg'
import HomePageImg4 from '../images/homepage4.svg'
import HomePageImg5 from '../images/homepage5.jpg'
import HomePageImg6 from '../images/homepage6.png'
import HomePageImg7 from '../images/homepage7.png'
import HomePageImg8 from '../images/homepage8.svg'
import HomePageImg9 from '../images/homepage9.svg'
import { claimTask1Box, claimTask2Box, getBuyNumber, getCount, getPersonalInfo, joinGuild, mintBlindBox } from '../features/api';
import { updateBasicInfo } from '../features/common'
import { useAppSelector } from '../app/hooks'
import { userState } from '../features/userSlice'
import { calcWidthStyle, genPercent, genShareLink, paramsFromURL, updateCountdown, widthStyle } from '../util'
import { debounce, throttle } from 'throttle-debounce';

const ADDITIONAL_COUNT = 100;

const debounceJoin = debounce(1500, () => {
  joinGuild();
}, { atBegin: true });

const debounceClaim1 = debounce(1500, () => {
  claimTask1Box();
}, { atBegin: true })

const debounceClaim2 = debounce(1500, () => {
  claimTask2Box();
}, { atBegin: true })

export function HomePage() {
  const user = useAppSelector(userState);
  const [count, setCount] = useState(0);

  
  const debounceMint = debounce(1500, () => {
    console.log("debounce mint called");
    mintBlindBox().then((res) => {
      res && setCount(count+1);
    });
    updateBasicInfo();
  }, { atBegin: true });

  useEffect(() => {
    updateBasicInfo();

    getCount().then(res => {
      if (typeof res === 'number')
      setCount(res + ADDITIONAL_COUNT);
      else setCount(ADDITIONAL_COUNT);
    });
  
  }, []);



  const buyBlindBox = () => {
    debounceMint();
  }

  return (<div className="w-full flex items-center flex-col">
    <img src={HomePageImg1} alt="" className='mt-16 w-16' />
    <div className="mt-6 text-white text-4xl font-bold font-['Inter'] tracking-widest">District50</div>
    <div className="mt-6 text-center text-white text-xl font-semibold font-['PingFang SC'] tracking-wide">
      <div>1000 Genesis</div>
      <div>D50 x BabyDoge Mint</div>
    </div>
    <img src={HomePageImg2} alt="" className="w-40 h-36 mt-28" />
    <div className="mt-6 text-white text-2xl font-semibold font-['PingFang SC']">D50 x BabyDoge</div>
    <div className="mt-4 mx-4 text-center text-white text-sm font-normal font-['PingFang SC'] leading-snug">
      The D50xBabyDoge NFT series is divided into six levels by colour. The levels from highest to lowest are red, green, blue, white, yellow and purple.
    </div>
    <div className="mt-3 mx-4 text-center text-white text-sm font-normal font-['PingFang SC'] leading-snug">
      Opening the first D50xBabyDoge Genesis Box will randomly obtain one of these NFTs, with a small chance of obtaining the highest level one.
    </div>
    <div className="mt-3 mx-4 text-center text-white text-sm font-normal font-['PingFang SC'] leading-snug">
      More characters, battle, fusion, breeding and other gameplay will be rolled out, so that the D50xBabyDoge can give full play to the unique interoperability of NFT, making the game even more interesting.
    </div>
    <div className="w-96 mt-6 bg-black bg-opacity-50 rounded-2xl flex flex-col items-center">
      <div className="mt-10 text-white text-base font-medium font-['PingFang SC']">{genPercent(count, 1000)}</div>
      <div className="w-72 h-3 relative mt-4">
        <div className="w-72 h-3 left-0 top-0 absolute bg-white bg-opacity-10 rounded-lg border border-white border-opacity-10" />
        <div style={calcWidthStyle(count, 1000)} className="h-3 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-lg shadow-inner" />
      </div>
      <CountDown></CountDown>
    </div>
    {(() => {
      return <div onClick={buyBlindBox} className="mt-6 w-72 h-14 bg-gradient-to-r from-teal-300 to-yellow-300 rounded-lg  flex items-center justify-center">
        <img className="w-6 h-4 relative mr-3" src={HomePageImg3} alt="" />
        <div className="text-stone-900 text-base font-bold font-['Inter'] tracking-wide">BUY A BLIND BOX</div>
      </div>
    })()}
    <JoinTheGuild hasGuild={user.hasGuild}></JoinTheGuild>
    <img src={HomePageImg5} alt="" className="mx-3 w-96 h-44 rounded-xl mt-6" />
    <div className="mt-6 text-white text-lg font-semibold font-['PingFang SC'] leading-7">D50xBabyDoge NFT is Coming Out</div>
    <div className="mt-6 mx-4 text-center text-white text-sm font-normal font-['PingFang SC'] leading-tight">This new NFT series will be a key component of the District50 platform, bringing fun and opportunities to the community.</div>
    <div className="mt-3 mx-4 text-center text-white text-sm font-normal font-['PingFang SC'] leading-tight">As the project steadily develops, there will be more cross-chain interactions in the future, including interactions with Arbitrum chain and MOMO players on Arbitrum chain.</div>
    <div className="mt-3 mx-4 text-center text-white text-sm font-normal font-['PingFang SC'] leading-tight">Members of the District50 community are welcome to join us in exploring the mysterious and fun-filled world of Doge!</div>
    <div className="mt-3 mx-4 text-center text-white text-sm font-normal font-['PingFang SC'] leading-tight">Please visit the official District50 channel for more information about D50xBabyDoge NFT. Thank you for your support!</div>

    <div className="mx-3 mt-6 pb-6 w-94 bg-black bg-opacity-50 rounded-2xl  flex items-center flex-col">
      <div className="mt-6 text-white text-lg font-medium font-['PingFang SC']">Rewards for Reaching Goals</div>
      <RewReachingGoals user={user}></RewReachingGoals>
    </div>

    <div className="mx-3 mt-2 pb-6 w-94 bg-black bg-opacity-50 rounded-2xl  flex items-center flex-col">
      <div className="mt-6 text-white text-lg font-medium font-['PingFang SC']">Rewards for Invitation</div>
      <RewInvitation user={user}></RewInvitation>
    </div>

    <div onClick={() => {genShareLink(false, user.account)}} className="w-64 h-12 mt-3 mb-5 bg-teal-300 rounded shadow-inner flex items-center justify-center">
      <div className="mr-1 text-center text-stone-900 text-base font-medium font-['PingFang SC']">Share Inviting Link</div>
      <img className="w-3.5 h-3.5" src={HomePageImg8} />
    </div>
  </div>)
}

function RewReachingGoals({ user }: { user: userState }) {
  return <div className="flex flex-col w-full relative">
    <div className="absolute left-11 top-8 w-1 h-24 bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
    <div id="step-1" className=" w-full flex mt-4">
      <div className="w-10 h-10 bg-white rounded-full shadow flex items-center justify-center ml-6 mr-4 shrink-0 flex-grow-0">
        <img src={HomePageImg2} className="w-6 h-5" alt="" />
      </div>
      <div className="mr-3 w-full">
        <div className=" text-white text-xs font-medium font-['PingFang SC'] w-full">Purchase 5 Blind Boxes and Get One for Free</div>
        <div className="flex justify-between w-full">
          <div className="w-full">
            <div className="w-40 h-2 relative mt-3">
              <div className="w-40 h-2 left-0 top-0 absolute bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
              <div style={widthStyle(20*(user.buyNumber < 5 ? user.buyNumber : 5))} className={`w-${40 / 5 * (user.buyNumber < 5 ? user.buyNumber : 5)} h-2 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-md shadow-inner`} />
            </div>
            <div className="text-white text-xs font-medium font-['PingFang SC'] mt-1">{(user.buyNumber < 5 ? user.buyNumber : 5) + '/5'}</div>
          </div>
          <ClaimedButton enabled={user.buyNumber >= 5} claimed={user.task1ClaimStatus} onClick={() => {debounceClaim1()}}/>
        </div>
      </div>
    </div>
    <div id="step-2" className=" w-full flex mt-6 ">
      <div className="w-16 h-16 relative ml-4 mr-1 flex items-center justify-center shrink-0 flex-grow-0">
        <div className="z-10 relative w-10 h-10 bg-white rounded-full shadow flex items-center justify-center shrink-0 flex-grow-0">
          <img src={HomePageImg2} className="w-6 h-5 shrink-0 grow-0" alt="" />
        </div>
        <img className="z-0 absolute w-16 h-16 left-0 top-0 shrink-0 grow-0 rotate-180" src={HomePageImg7} />
      </div>
      <div className="mr-3 w-full">
        <div className=" text-white text-xs font-medium font-['PingFang SC']">Purchase 10 Blind Boxes and Get 0.01% of Team's Unlocked Tokens</div>
        <div className="flex justify-between w-full">
          <div className="w-full">
            <div className="w-40 h-2 relative mt-3">
              <div className="w-40 h-2 left-0 top-0 absolute bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
              <div  style={widthStyle(10*(user.buyNumber < 10 ? user.buyNumber : 10))} className={`h-2 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-md shadow-inner`} />
            </div>
            <div className="text-white text-xs font-medium font-['PingFang SC'] mt-1">{(user.buyNumber < 10 ? user.buyNumber : 10) + '/10'}</div>
          </div>
          <ClaimedButton enabled={user.buyNumber >= 10} claimed={false} onClick={() => {alert('Congratulations! Please send an E-mail to us to claim the reward, and we will contact you within 3 working days after receiving the email.')}}/>
        </div>
      </div>
    </div>
  </div>
}

function RewInvitation({ user }: { user: userState }) {
  return <div className="flex flex-col w-full relative">
    <div className="absolute left-11 top-8 w-1 h-24 bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
    <div id="step-1" className=" w-full flex mt-4">
      <div className="w-10 h-10 bg-white rounded-full shadow flex items-center justify-center ml-6 mr-4 shrink-0 flex-grow-0">
        <img src={HomePageImg2} className="w-6 h-5" alt="" />
      </div>
      <div className="mr-3 w-full">
        <div className=" text-white text-xs font-medium font-['PingFang SC'] w-full">Purchase 5 Blind Boxes by 5 Players Invited and Get One for Free</div>
        <div className="flex justify-between w-full">
          <div className="w-full">
            <div className="w-40 h-2 relative mt-3">
              <div className="w-40 h-2 left-0 top-0 absolute bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
              <div style={widthStyle(20*(user.childrenNumber < 5 ? user.childrenNumber : 5))} className={`h-2 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-md shadow-inner`} />
            </div>
            <div className="text-white text-xs font-medium font-['PingFang SC'] mt-1">{(user.childrenNumber < 5 ? user.childrenNumber : 5) + '/5'}</div>
          </div>
          <ClaimedButton enabled={user.childrenNumber >= 5} claimed={user.task2ClaimStatus} onClick={() => {debounceClaim2()}}/>
        </div>
      </div>
    </div>
    <div id="step-2" className=" w-full flex mt-6 ">
      <div className="w-16 h-16 relative ml-4 mr-1 flex items-center justify-center shrink-0 flex-grow-0">
        <div className="z-10 relative w-10 h-10 bg-white rounded-full shadow flex items-center justify-center shrink-0 flex-grow-0">
          <img src={HomePageImg2} className="w-6 h-5 shrink-0 grow-0" alt="" />
        </div>
        <img className="z-0 absolute w-16 h-16 left-0 top-0 shrink-0 grow-0 rotate-180" src={HomePageImg7} />
      </div>
      <div className="mr-3 w-full">
        <div className=" text-white text-xs font-medium font-['PingFang SC']">Purchase 10 Blind Boxes by 10 Players Invited and Get 0.01% of Team's Unlocked Tokens</div>
        <div className="flex justify-between w-full">
          <div className="w-full">
            <div className="w-40 h-2 relative mt-3">
              <div className="w-40 h-2 left-0 top-0 absolute bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
              <div style={widthStyle(10*(user.childrenNumber < 10 ? user.childrenNumber : 10))} className={`h-2 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-md shadow-inner`} />
            </div>
            <div className="text-white text-xs font-medium font-['PingFang SC'] mt-1">{(user.childrenNumber < 10 ? user.childrenNumber : 10) + '/10'}</div>
          </div>
          <ClaimedButton enabled={user.childrenNumber >= 10} claimed={false} onClick={() => {alert('Congratulations! Please send an E-mail to us to claim the reward, and we will contact you within 3 working days after receiving the email.')}}/>
        </div>
      </div>
    </div>
  </div>
}

function ClaimedButton ({enabled, claimed, onClick}: {enabled: boolean, claimed: boolean, onClick: () => any}) {
  const clickCallback = () => {
    if (!enabled || claimed) {
      return;
    }
    onClick();
  }
  if (claimed) return (<div onClick={clickCallback} className="flex-grow-0 shrink-0 mt-2 w-28 h-9  bg-stone-300 rounded-lg shadow-inner flex items-center justify-center">
    <div className="text-stone-900 text-sm font-normal font-['Inter']">Claimed</div>
  </div>)
  if (enabled && !claimed) return (<div onClick={clickCallback} className="flex-grow-0 shrink-0 mt-2 w-28 h-9 bg-teal-300 rounded-lg shadow-inner flex items-center justify-center">
    <div className="text-stone-900 text-sm font-normal font-['Inter']">{"Claim"}</div>
  </div>)
  else return (<div onClick={clickCallback} className="flex-grow-0 shrink-0 mt-2 w-28 h-9 bg-stone-300 rounded-lg shadow-inner flex items-center justify-center">
    <div className="text-stone-900 text-sm font-normal font-['Inter']">{"Claim" }</div>
  </div>)
}

function JoinTheGuild({hasGuild}: { hasGuild: boolean }) {
  if (paramsFromURL.guild && !hasGuild) return (<div onClick={() => {debounceJoin()}} className="w-72 h-10 rounded-lg border border-teal-300 mt-4 flex items-center justify-center" >
    <img className='w-6 h-4 mr-2' src={HomePageImg4} alt="" />
    <div className="text-teal-300 text-base font-bold font-['Inter'] tracking-wide">JOIN THE GUILD</div>
  </div>)
  else if (hasGuild) {
    return (<div className="w-72 h-10 border-0 mt-4 flex items-center justify-center">
      <img className='w-6 h-4 mr-2' src={HomePageImg9} alt="" />
      <div className="text-neutral-400 text-base font-bold font-['Inter'] tracking-wide">JOINED</div>
    </div>)
  }
}

function CountDown () {
  const [countDown, setCountDown] = useState(updateCountdown());

  useEffect(() => {
    const countdownInterval = setInterval(() => {
      setCountDown(updateCountdown);
    }, 1000);
    return () => {
      clearInterval(countdownInterval);
    }
  }, []);
  return <div className='mx-14 flex items-center mt-8 mb-11'>
  <div className="w-14 h-14 bg-gray-800 rounded-2xl flex items-center justify-center">
    <div className="text-white text-lg font-bold font-['Inter']">{countDown[0] + 'D'}</div>
  </div>
  <div className="text-white text-lg font-bold font-['Inter'] w-4 flex items-center justify-center">
    <div>:</div>
  </div>
  <div className="w-14 h-14 bg-gray-800 rounded-2xl flex items-center justify-center">
    <div className="text-white text-lg font-bold font-['Inter']">{countDown[1] + 'H'}</div>
  </div>
  <div className="text-white text-lg font-bold font-['Inter'] w-4 flex items-center justify-center">
    <div>:</div>
  </div>
  <div className="w-14 h-14 bg-gray-800 rounded-2xl flex items-center justify-center">
    <div className="text-white text-lg font-bold font-['Inter']">{countDown[2] + 'M'}</div>
  </div>
  <div className="text-white text-lg font-bold font-['Inter'] w-4 flex items-center justify-center">
    <div>:</div>
  </div>
  <div className="w-14 h-14 bg-gray-800 rounded-2xl flex items-center justify-center">
    <div className="text-white text-lg font-bold font-['Inter']">{countDown[3] + 'S'}</div>
  </div>
</div>
}

export default HomePage;