
import LineImg from '../images/line.svg'
import AddressImg from '../images/AddressInvited.png'
import BindImg from '../images/Bind.png'
import currentImg from '../images/Current.png'
import CommissionImg from '../images/Commission.png'
import TokenNameImg from '../images/TokenName.png'
import TokenInfoImg from '../images/TokenInfo.png'
import { useEffect, useState } from 'react'
import { getGuildBuyNumber, getReward, setRate } from '../features/api'
import { useAppSelector } from '../app/hooks'
import { userState } from '../features/userSlice'
import PersonInfoModal from './PersonInfoModal'

const personalInfoData = [
    { 'key': '1', 'value': '1', 'name': 'Address Invited', 'img': AddressImg },
    { 'key': '2', 'value': '2', 'name': 'Blind Boxes Purchased', 'img': BindImg },
    { 'key': '3', 'value': '3', 'name': 'Current Commission', 'img': currentImg },
    { 'key': '4', 'value': '4', 'name': 'Commission Earned', 'img': CommissionImg },
]

const commissionEarnedData = [
    { 'key': '1', 'value': '1', 'name': 'ETH', 'img': TokenNameImg },
    { 'key': '2', 'value': '2', 'name': 'Token', 'img': TokenInfoImg },
]

const lineDom = () => {
    return <img
        className="w-full inline-block stroke-1 stroke-[var(--color-whites-w-20, rgba(255, 255, 255, 0.20))]"
        src={LineImg}
    />
}


export function GuildPage({members}: {members: string[]}) {
    const fontStyle1 = {
        background: 'linear-gradient(90deg, #3BFDEC 0%, #EDFD3B 100%)',
        backgroundClip: 'text',
        WebkitBackgroundClip: 'text',
        WebkitTextFillColor: 'transparent',
    }
    // const [guildBuyNum, setGuildBuyNum] = useState(0);
    const [reward, setReward] = useState(0);

    const user = useAppSelector(userState);

    useEffect(() => {
        // getGuildBuyNumber().then((res)=>{
        //     if (typeof res === 'number')
        //     setGuildBuyNum(res);
        // });
        getReward().then((res)=>{
            if (typeof res === 'number')
            setReward(res);
        });
    }, [user.account]);


    return <div className="w-full bg-slate-950 bg-opacity-90 rounded-lg p-2.5 text-gray-900">
        <div id="my-person-info" className="w-full bg-gray-900 rounded-lg px-4 pt-2.5 pb-4">
                {/* <div className='flex justify-between bg-slate-950 rounded-lg p-1 mb-6'>
                    <div className='text-white text-base font-medium px-3 py-2.5'>Address</div>
                    <span className='bg-white flex justify-center items-center px-2 py-1 rounded-lg'>0x...1234</span>
                </div> */}
                <div className="">
                    {personalInfoData?.map(item => {
                        return (item.key === '1' || item.key === '2' || user.rate) ? <div key={item.key}>
                            <div className='flex justify-between mb-3 mt-3' >
                                <div className='flex items-center'>
                                    <img className='w-4 h-4 mr-2' src={item.img}></img>
                                    <div className='text-white font-sans text-sm font-normal tracking-normal'>{item.name}</div>
                                </div>
                                <div className='bg-slate-950 h-17.5 rounded-lg'>
                                    {item.key == '4' && commissionEarnedData?.map(i=>{
                                        return <div className='pl-3 h-10 w-28 flex items-center text-white text-xs' key={`${item.key}-${i.key}`}>
                                            <img className='w-3 h-3 mr-2' src={i.img}></img>

                                            <div className='text-xs'>{i.name}:</div>
                                            <div className='text-xs'>
                                                {i.key === '1' && reward / 1e18}
                                                {i.key === '2' && 0}
                                            </div>
                                        </div>
                                    })}
                                    {item.key != '4' && <div className='text-lg pl-3 text-white w-28 flex items-center bg-slate-950 rounded-md'>
                                        {item.key === '1' && members.length}
                                        {item.key === '2' && user.buyNumber}
                                        {item.key === '3' && Math.floor(user.rate) + "%"}
                                    </div>}
                                </div>
                            </div>
                            {item.key == '2' && <div>{lineDom()}</div>}
                        </div> : ''
                    })}
                </div>
        </div>
        <div></div>


        {<div id='person-info-part3' className="w-full bg-gray-900 rounded-lg pb-4 mt-3">
            <div className="w-full h-20 leading-10 bg-neutral-900 rounded pt-4 pb-6 px-4 bg-clip-content text-center text-white text-base font-medium">Addresses Invited</div>
            <div className='px-4 w-full bg-clip-content'>
                <GuildList members={members} user={user}></GuildList>
            </div>
        </div>}
    </div>
}

export function GuildList({ members, user }: { members: string[], user: userState }) {
    return <ul className='w-full  text-white text-xs font-normal list-none'>
        {members.map((str, i) => (<li key={str}>
            <div className='inline-block w-5'>{i + 1}.</div> {str.slice(0, 10) + '...' + str.slice(-10)} <br />
            <div className='w-full overflow-hidden whitespace-nowrap'>
                <div className='inline-block w-5'></div>
                <img className="w-full inline-block stroke-1 stroke-[var(--color-whites-w-20, rgba(255, 255, 255, 0.20))]" src={LineImg} />
                <div className='flex justify-between pl-5'>
                    <div className='text-#BDC0C8 '>2023.10.22. 19:00</div>
                    {user.rate ? <PersonInfoModal member={str} rate={user.rate}></PersonInfoModal>  : ''}
                    {/* <div onClick={() => {setRate(str, 30)}} className='flex justify-center items-center bg-white rounded-lg text-xs px-2 py-1 text-black'>Setting </div> */}
                </div>
            </div>
        </li>))}
    </ul>
}


export default GuildPage;