
import ShageImg from '../images/ShareImg.svg'
import Rectangle from '../images/Rectangle-204.png'

import Group from '../images/Group-724.png'
import { choose } from '../features/curpageSlice'
import { useAppDispatch, useAppSelector } from '../app/hooks'
import { useEffect, useState } from 'react'
import { claimGuildReward, getGuildBuyNumber, guildRewardClaimStatus } from '../features/api'
import { calcWidthStyle, genPercent } from '../util'
import { userState } from '../features/userSlice'
import GuildModal from './GuildModal'
import { debounce } from 'throttle-debounce'

const debounceGetReward = debounce(1500, (n: number) => {
    claimGuildReward(n);
    }, {atBegin: true});

export function GuildPage() {
    const fontStyle1 = {
        background: 'linear-gradient(90deg, #3BFDEC 0%, #EDFD3B 100%)',
        backgroundClip: 'text',
        WebkitBackgroundClip: 'text',
        WebkitTextFillColor: 'transparent',
    }

    const user = useAppSelector(userState);
    const [guildBuyNum, setGuildBuyNum] = useState(0);
    const [rewardClaimStatus, setRewardClaimStatus] = useState([]);

    const taskNums = [3,4,5,6,7,8]

    useEffect(() => {
        getGuildBuyNumber().then((res)=>{
            res && setGuildBuyNum(res);
        });
        guildRewardClaimStatus().then((res) => {
            res && setRewardClaimStatus(res);
        });
    }, [user.account]);

    const dispatch = useAppDispatch();

    return <div className="w-full bg-slate-950 bg-opacity-90 rounded-lg pt-3 pb-4 px-2.5">

        <div id='tark-list-part1' className="w-full bg-gray-900 rounded-lg px-2.5 pt-2.5 pb-4 bg-clip-contents mb-2.5">

            <div className='bg-[#0D1019] mb-3 rounded-lg px-3 py-3'>
                <div className='flex justify-between'>
                    <div className='w-40 h-17 font-["PingFang SC"] text-transparent bg-gradient-to-r bg-clip-text from-[#3BFDEC] to-[#EDFD3B] text-xs tracking-tight'>
                        Complete the tasks listed,
                        invite more players to purchase
                        blind boxes, and earn more
                        rewards!
                    </div>
                    <div>
                        <img className='w-20 h-16' src={Group} alt="" />
                    </div>
                </div>


                <div className='font-normal text-white font-[PingFang SC]'>Event Time: 2023.10.25 - 2024.1.31</div>
            </div>


            <div id="guild-buttons" className='flex items-center justify-between w-full'>
                {/* <div className="w-36 h-12 bg-teal-300 leading-12 rounded text-center text-stone-900 text-base font-medium ">
                    Share <img className="w-3.5 h-3.5 inline-block mb-1" src={ShageImg}></img>
                </div> */}
                <GuildModal user={user.account}></GuildModal>
                <div onClick={() => {dispatch(choose(0))}} className="w-36 h-12 bg-white leading-12 rounded text-center text-stone-900 text-base font-medium ">Go Now</div>
            </div>


        </div>
        <div id='tark-list-part2' className="w-full bg-gray-900 rounded-lg px-2.5 pt-2.5 pb-4 bg-clip-contents">
            <div id='task1' className='rounded-lg px-2.5 pt-2.5 pb-4 bg-clip-contents bg-[#0D1019] mb-2'>
                <div className='flex justify-between'>
                    <div className="text-white font-medium text-base font-['PingFang SC']">Task Ⅰ


                    </div>
                    <div className="text-white font-medium text-base font-['PingFang SC']">{genPercent(guildBuyNum, taskNums[0])}

                    </div>
                </div>
                <div className="text-white font-medium text-base font-['PingFang SC']">
                    Purchase 5 Blind Boxes by Players Invited and Get One for Free

                </div>
                {/* <div className="h-2 w-full rounded-sm text-white font-medium text-base font-['PingFang SC'] shadow-inset-top-sm shadow-inset-bottom-sm">
                </div> */}
                <div className="h-2 my-2 relative">
                    <div className="w-full h-2 left-0 top-0 absolute bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
                    <div style={calcWidthStyle(guildBuyNum,taskNums[0])} className="h-2 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-md shadow-inner" />
                </div>
                <ClaimedButton enabled={guildBuyNum >= taskNums[0]} claimed={rewardClaimStatus[0]} onClick={() => {debounceGetReward(1)}}></ClaimedButton>
            </div>

            <div id='task2' className='rounded-lg px-2.5 pt-2.5 pb-4 bg-clip-contents bg-[#0D1019] mb-2'>
                <div className='flex justify-between'>
                    <div className="text-white font-medium text-base font-['PingFang SC']">Task Ⅱ

                    </div>
                    <div className="text-white font-medium text-base font-['PingFang SC']">{genPercent(guildBuyNum, taskNums[1])}
                    </div>
                </div>
                <div className="text-white font-medium text-base font-['PingFang SC']">
                    Purchase 10 Blind Boxes by Players Invited and Get Two for Free
                </div>
                <div className="h-2 w-full rounded-sm text-white font-medium text-base font-['PingFang SC'] shadow-inset-top-sm shadow-inset-bottom-sm">
                </div>
                <div className="h-2 my-2 relative">
                    <div className="w-full h-2 left-0 top-0 absolute bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
                    <div style={calcWidthStyle(guildBuyNum, taskNums[1])} className="h-2 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-md shadow-inner" />
                </div>
                <ClaimedButton enabled={guildBuyNum >= taskNums[1]} claimed={rewardClaimStatus[1]} onClick={() => {debounceGetReward(2)}}></ClaimedButton>
            </div>

            <div id='task3' className='rounded-lg px-2.5 pt-2.5 pb-4 bg-clip-contents bg-[#0D1019] mb-2'>
                <div className='flex justify-between'>
                    <div className="text-white font-medium text-base font-['PingFang SC']">Task Ⅲ
                    </div>
                    <div className="text-white font-medium text-base font-['PingFang SC']">{genPercent(guildBuyNum, taskNums[2])}
                    </div>
                </div>
                <div className="text-white font-medium text-base font-['PingFang SC']">
                    Purchase 20 Blind Boxes by Players Invited and Get Three for Free

                </div>
                <div className="h-2 w-full rounded-sm text-white font-medium text-base font-['PingFang SC'] shadow-inset-top-sm shadow-inset-bottom-sm">
                </div>
                <div className="h-2 my-2 relative">
                    <div className="w-full h-2 left-0 top-0 absolute bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
                    <div style={calcWidthStyle(guildBuyNum, taskNums[2])} className="h-2 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-md shadow-inner" />
                </div>
                <ClaimedButton enabled={guildBuyNum >= taskNums[2]} claimed={rewardClaimStatus[1]} onClick={() => {debounceGetReward(3)}}></ClaimedButton>
            </div>
            <div id='task4' className='rounded-lg px-2.5 pt-2.5 pb-4 bg-clip-contents bg-[#0D1019] mb-2'>
                <div className='flex justify-between'>
                    <div className="text-white font-medium text-base font-['PingFang SC']">Task Ⅳ
                    </div>
                    <div className="text-white font-medium text-base font-['PingFang SC']">{genPercent(guildBuyNum, taskNums[3])}
                    </div>
                </div>
                <div className="text-white font-medium text-base font-['PingFang SC']">
                    Purchase 30 Blind Boxes by Players Invited and Get Ten for Free
                </div>
                <div className="h-2 w-full rounded-sm text-white font-medium text-base font-['PingFang SC'] shadow-inset-top-sm shadow-inset-bottom-sm">
                </div>
                <div className="h-2 my-2 relative">
                    <div className="w-full h-2 left-0 top-0 absolute bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
                    <div style={calcWidthStyle(guildBuyNum, taskNums[3])} className="h-2 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-md shadow-inner" />
                </div>
                <ClaimedButton enabled={guildBuyNum >= taskNums[3]} claimed={rewardClaimStatus[3]} onClick={() => {debounceGetReward(4)}}></ClaimedButton>
            </div>
            <div id='task5' className='bg-gradient-to-r from-teal-400 to-yellow-400 rounded-lg px-2.5 pt-2.5 pb-4 bg-clip-contents mb-2'>
                <div className='flex justify-between'>
                    <div className="text-#1D1C16 font-medium text-base font-['PingFang SC']">Task Ⅴ
                    </div>
                    <div className="text-#1D1C16 font-medium text-base font-['PingFang SC']">{genPercent(guildBuyNum, taskNums[4])}
                    </div>
                </div>
                <div className="text-#1D1C16 font-medium text-base font-['PingFang SC']">
                    Purchase 50 Blind Boxes by Players Invited and Get 0.1% of Team's Unlocked Tokens
                </div>
                <div className="h-2 w-full rounded-sm text-white font-medium text-base font-['PingFang SC'] shadow-inset-top-sm shadow-inset-bottom-sm">
                </div>
                <div className="text-#346763 h-2 my-2 font-normal text-sm font-['Inter'] bg-black bg-opacity-10 rounded-md bg">
                    <div style={calcWidthStyle(guildBuyNum,taskNums[4])} className='h-2 bg-black rounded-md'></div>
                </div>
                <ClaimedButton enabled={guildBuyNum >= taskNums[4]} claimed={false} onClick={() => {alert('Congratulations! Please send an E-mail to us to claim the reward, and we will contact you within 3 working days after receiving the email.')}}></ClaimedButton>
            </div>
            <div id='task6' className='rounded-lg px-2.5 pt-2.5 pb-4 bg-clip-contents bg-[#0D1019] mb-2'>
                <div className='flex justify-between'>
                    <div className="text-white font-medium text-base font-['PingFang SC']">Task Ⅵ
                    </div>
                    <div className="text-white font-medium text-base font-['PingFang SC']">{genPercent(guildBuyNum, taskNums[5])}
                    </div>
                </div>
                <img className='w-full' src={Rectangle} />
                <div className="text-white font-medium text-base font-['PingFang SC']">
                    Purchase 60 Blind Boxes by Players Invited and Get One Swiss Watch with Special Logo (valued at $2,000)
                </div>
                <div className="h-2 w-full rounded-sm text-white font-medium text-base font-['PingFang SC'] shadow-inset-top-sm shadow-inset-bottom-sm">
                </div>
                <div className="h-2 my-2 relative">
                    <div className="w-full h-2 left-0 top-0 absolute bg-white bg-opacity-10 rounded-md border border-white border-opacity-10" />
                    <div style={calcWidthStyle(guildBuyNum,taskNums[5])} className="h-2 left-0 top-0 absolute bg-gradient-to-r from-teal-300 to-yellow-300 rounded-md shadow-inner" />
                </div>
                <ClaimedButton enabled={guildBuyNum >= taskNums[5]} claimed={false} onClick={() => {alert('Congratulations! Please send an E-mail to us to claim the reward, and we will contact you within 3 working days after receiving the email.')}}></ClaimedButton>
            </div>
        </div>







    </div>
}

function ClaimedButton ({enabled, claimed, onClick}: {enabled: boolean, claimed: boolean, onClick: () => any}) {
    const clickCallback = () => {
      if (!enabled || claimed) {
        return;
      }
      onClick();
    }
    if (claimed) return (<div onClick={clickCallback} className='flex justify-end items-center'>
        <div className='font-#8E8E8E justify-center flex items-center rounded-lg h-9 w-20 bg-[#CECECE]'>Claimed</div>
    </div>)
    if (enabled && !claimed) return (<div onClick={clickCallback} className='flex justify-end items-center'>
    <div className='font-#1D1C16 justify-center flex items-center rounded-lg h-9 w-20 bg-[#3BFDEC]'>Claim</div>
</div>)
    else return (<div onClick={clickCallback} className='flex justify-end items-center'>
    <div className='font-#8E8E8E justify-center flex items-center rounded-lg h-9 w-20 bg-[#CECECE]'>Claim</div>
</div>)
  }


export default GuildPage;

