import logo from "./logo.svg";
import "./App.css";


import NavBar from "./components/navbar";
import Bg1 from "./images/bg1.jpg";
import InfoPage from "./components/infopage";
import Vector from "./images/vector1.svg";
import Vector2 from "./images/vector1.svg";
import HomePage from "./components/homepage";
import { useAppSelector } from "./app/hooks";
import { selectCurpage } from "./features/curpageSlice";
import BagPage from "./components/bagpage";
import AnnoPage from "./components/annopage";
import { connectWallet } from "./features/api";
import { useEffect } from "react";
import { updateBasicInfo } from "./features/common";
import { userState } from "./features/userSlice";

function App() {
  const curpage = useAppSelector(selectCurpage);
  const user = useAppSelector(userState);

  useEffect(() => {
    connectWallet();

    const timer = setInterval(() => {
      updateBasicInfo();
    }, 2000);

    return () => {
      clearInterval(timer);
    }
  }, [])
  
  const bgStyle = {
    background:
    `linear-gradient(to right, rgba(0, 0, 0, 0.6), rgba(100, 100, 100, 0.3), rgba(0, 0, 0, 0.6)), url(${Bg1}) center/cover no-repeat`
  }
  return (
    <div className={`relative overflow-hidden min-h-screen`} style={bgStyle}>
      <img className="absolute w-full z-2" style={{top: '25rem'}} src={Vector} alt="" />
      <img className="absolute w-full z-1 top-96 opacity-50" src={Vector2} alt="" />
      <div className="w-full absolute bottom-0 z-1 bg-bg-bl" style={{height: 'calc(100% - 30rem)'}}></div>
      <NavBar></NavBar>

      <div className="w-full z-10 relative">
        {curpage === 0 && <HomePage></HomePage>}
        {curpage === 1 && <InfoPage></InfoPage>}
        {curpage === 2 && <BagPage></BagPage>}
        {curpage === 3 && <AnnoPage></AnnoPage>}
        
      </div>
    </div>
  )
}

export default App
