import { Tabs } from '@mui/base/Tabs';
import { TabsList } from '@mui/base/TabsList';
import { TabPanel } from '@mui/base/TabPanel';
import { Tab } from '@mui/base/Tab';

import { useEffect, useState } from 'react';
import Modal from './GuildModal'
import TdImg from '../images/imgtd.jpg'
import EventImg from '../images/eventimg.jpg'
import ShageImg from '../images/ShareImg.svg'
import LineImg from '../images/line.svg'
import { getChildrenList } from '../features/api';
import { genShareLink, getRandomPubKeys, shuffleArray, sliceStr } from '../util';
import { useAppDispatch, useAppSelector } from '../app/hooks';
import { choose } from '../features/curpageSlice';
import { userState } from '../features/userSlice';
import GuildModal from './GuildModal';


export function GuildPage({members}: {members: string[]}) {
  const fontStyle1 = {
    background: 'linear-gradient(90deg, #3BFDEC 0%, #EDFD3B 100%)',
    backgroundClip: 'text',
    WebkitBackgroundClip: 'text',
    WebkitTextFillColor: 'transparent',
  }

  const user = useAppSelector(userState);

  const dispatch = useAppDispatch();

  return <div className="w-full bg-slate-950 bg-opacity-90 rounded-lg flex flex-col content-center items-center">
    <div id='guildpage-part1' className="w-full h-48 bg-gray-900 rounded-lg px-2.5 pt-2.5 pb-4 bg-clip-content flex flex-col content-center items-center">
      <div id="my-guild" className="w-full h-18 bg-[#0D1019] rounded px-4 py-4 bg-clip-content text-center align-bottom"><span className=' text-white text-base font-medium '>My Guild</span></div>
      <div className='flex justify-between w-full px-4'>
        <div className='flex flex-col  justify-between'>
          <div className="w-44 h-10 bg-[#0D1019] rounded flex items-center">
            <div id="info-tag-1" className='text-stone-300 text-xs font-normal ml-3 inline-block w-1/3'>Name</div>
            <div id="info-tag-2" className='text-white text-xs font-medium'>Golden Wing</div>
          </div>
          <div className="w-44 h-10 bg-[#0D1019] rounded flex items-center">
            <div id="info-tag-1" className='text-stone-300 text-xs font-normal ml-3 inline-block w-1/3'>Rank</div>
            <div id="info-tag-2" className='text-white text-xs font-medium'>2</div>
          </div>
        </div>
        <div className="w-24 h-24 bg-[#0D1019] rounded flex justify-center items-center">
          <div className="w-16 h-16 bg-gray-900 rounded shadow flex justify-center items-center">
            <img className="w-12 h-12 rounded block" src={TdImg} />
          </div>
        </div>
      </div>
    </div>
    <div id='guildpage-part2' className="w-full bg-gray-900 rounded-lg  px-2.5 pb-3 bg-clip-content flex flex-col content-center items-center overflow-visible">
      <div className='px-4 w-full pb-2 pt-4'>
        <img className="w-full h-28 rounded-lg border-2 border-black object-cover" src={EventImg} />
      </div>
      <div className="w-full h-16 px-4 pb-3 bg-clip-content bg-[#0D1019] rounded flex flex-col content-center items-center">
        <div className="w-64 text-center text-base font-medium bg-clip-text pb-1 pt-2" style={fontStyle1}>
          Blind Boxes for Invitation Event
        </div>
        <div className="text-center text-slate-300 text-xs font-medium font-['PingFang SC']">Event Time: 10.25-11.20</div>
      </div>
      <div id="guild-buttons" className='flex content-center items-center justify-between w-full px-4 pb-4'>
        {/* <div onClick={() => {genShareLink(true, user.account)}} className="w-36 h-12 bg-teal-300 leading-12 rounded text-center text-stone-900 text-base font-medium ">
          Share <img className="w-3.5 h-3.5 inline-block mb-1" src={ShageImg}></img>
        </div> */}
        <GuildModal user={user.account}></GuildModal>
        <div onClick={() => {dispatch(choose(0))}} className="w-36 h-12 bg-white leading-12 rounded text-center text-stone-900 text-base font-medium ">Go Now</div>
      </div>
    </div>
    <div id='guildpage-part3' className="w-full h-72 bg-gray-900 rounded-lg  px-2.5 pb-2.5 bg-clip-content flex flex-col content-center items-center overflow-visible">
      <div className="w-full h-20 leading-10 bg-[#0D1019] rounded pt-4 pb-6 px-4 bg-clip-content text-center text-white text-base font-medium">Members</div>
      <div className='px-4 w-full bg-clip-content'>
        <GuildList members={shuffleArray([...members, ...getRandomPubKeys(4)]).map(v => sliceStr(v, 10, 10))}></GuildList>
      </div>
    </div>
  </div>
}

export function GuildList ({members}: {members: string[]}) {
  return <ul className='w-full  text-white text-xs font-normal list-none'>
    {members.map((str, i) => (<li key={str}>
      <div className='inline-block w-5'>{i+1}.</div> {str.slice(0,10)+'...'+str.slice(-10)} <br />
      <div className='w-full overflow-hidden whitespace-nowrap h-6'>
        <div className='inline-block w-5'></div> 
        <img className="w-full inline-block stroke-1 stroke-[var(--color-whites-w-20, rgba(255, 255, 255, 0.20))]" src={LineImg} />
        {/* <div className='inline-block whitespace-nowrap overflow-hidden  text-opacity-50'>----------------------------------------------------------------------------</div> */}
      </div>
    </li>))}
  </ul>
}

export default GuildPage;