import { store } from "../app/store";
import { getBuyNumber, getChildrenNumber, getPersonalInfo, getTask1ClaimedStatus, getTask2ClaimedStatus, getTotalNumber } from "./api";
import { buyNumber, childrenNumber, info, task1, task2, totalNumber } from "./userSlice";

export function updateBasicInfo() {
  const {user} = store.getState();
  if (!user.account) {
    console.log("no account, stop update");
    return;
  }
  getBuyNumber().then((n) => {
    store.dispatch(buyNumber(n));
  });
  getTotalNumber().then((n) => {
    store.dispatch(totalNumber(n));
  });
  getPersonalInfo().then((res) => {
    store.dispatch(info(res));
  });
  getChildrenNumber().then((res) => {
    store.dispatch(childrenNumber(res));
  });
  getTask1ClaimedStatus().then((res) => {
    store.dispatch(task1(res));
  });
  getTask2ClaimedStatus().then((res) => {
    store.dispatch(task2(res));
  });
  console.log(user);
}