
import { Modal as BaseModal } from '@mui/base/Modal';
import React from 'react'
import clsx from 'clsx';
import { styled, Box, Theme } from '@mui/system';
import LikeImg from '../images/Unionlike.png'
import { useState } from 'react';
import CloseImg from '../images/close.png'
import ShageImg from '../images/ShareImg.svg'
import { genShareLink } from '../util';

const GuildModal = ({user}: {user: string}) => {
    const [open, setOpen] = useState(false);
    const [radioId, setId] = useState('2')
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const handleDone = () => {
        setOpen(false);
        if (radioId === '1') {
            genShareLink(false, user);
        }
        else if (radioId === '2') {
            genShareLink(true, user);
        }
        handleClose();
    }
    return <div>
        <TriggerButton onClick={handleOpen}>
            Share<img className="w-3.5 h-3.5 inline-block mb-1" src={ShageImg}></img>
        </TriggerButton>
        <Modal
            aria-labelledby="unstyled-modal-title"
            aria-describedby="unstyled-modal-description"
            open={open}
            onClose={handleClose}
            slots={{ backdrop: StyledBackdrop }}
        >
            <Box sx={style}>
                <div className='w-72 h-36 bg-[#1A1F2C] relative'>
                    <img  onClick={handleClose} src={CloseImg} alt="" className='absolute right-1.5 top-1.5 w-3 h-3'/>
                    <h2 id="unstyled-modal-title" className='text-white'>Share</h2>
                    <div className='mt-2 h-5 flex items-center text-white'>
                        {radioId === '1' ? <div className='h-3 w-3 rounded-full bg-[#3BFDEC] flex justify-center items-center'>
                            <img className="w-1" src={LikeImg} alt="" /> </div> : <input className='h-3 w-3' type="radio" name="10%" id="radio1" onClick={() => { setId('1') }} />}

                        <div className={radioId === '1'?'ml-2 text-[#3BFDEC]':'ml-2' }>Regular inviting link</div>
                    </div>

                    <div className='mt-2 h-5 flex items-center text-white'>
                        {radioId === '2' ? <div className='h-3 w-3 rounded-full bg-[#3BFDEC] flex justify-center items-center'>
                            <img className="w-1" src={LikeImg} alt="" /> </div> : <input className='h-3 w-3' type="radio" name="10%" id="radio1" onClick={() => { setId('2') }} />}

                        <div className={radioId === '2'?'ml-2 text-[#3BFDEC]':'ml-2' }>Guild inviting link</div>
                    </div>

                    <div onClick={handleDone} className='text-[#1D1C16] mt-5 bg-[#3BFDEC] w-full h-8 rounded-lg flex items-center justify-center'>Done</div>
                </div>

            </Box>
        </Modal>

    </div>
}
export default GuildModal
const Backdrop = React.forwardRef<
    HTMLDivElement,
    { open?: boolean; className: string }
>((props, ref) => {
    const { open, className, ...other } = props;
    return (
        <div
            className={clsx({ 'MuiBackdrop-open': open }, className)}
            ref={ref}
            {...other}
        />
    );
});

const blue = {
    200: '#99CCF3',
    400: '#3399FF',
    500: '#007FFF',
};

const grey = {
    50: '#f6f8fa',
    100: '#eaeef2',
    200: '#d0d7de',
    300: '#afb8c1',
    400: '#8c959f',
    500: '#6e7781',
    600: '#57606a',
    700: '#424a53',
    800: '#32383f',
    900: '#24292f',
};

const Modal = styled(BaseModal)`
  position: fixed;
  z-index: 1300;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const StyledBackdrop = styled(Backdrop)`
  z-index: -1;
  position: fixed;
  inset: 0;
  background-color: rgb(0 0 0 / 0.5);
  -webkit-tap-highlight-color: transparent;
`;

