import AnnoNone from "../images/annon-none.svg"
function AnnoPage() {
  return <div className="w-full flex flex-col content-center items-center">
    <div className="mt-3 text-white text-lg font-medium font-['PingFang SC']">Announcement</div>
    <div className="mt-3 w-90 h-96 bg-slate-950 bg-opacity-90 rounded-lg flex flex-col content-center items-center">
        <img className="w-16 h-16 mt-10" src={AnnoNone} alt="" />
        <div className="mt-3 text-neutral-500 text-sm font-normal font-['PingFang SC']">No announcement</div>
    </div>
  </div>
}

export default AnnoPage;